@import url(https://fonts.googleapis.com/css2?family=Zen+Kaku+Gothic+Antique:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Zen Kaku Gothic Antique', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:rgb(6,18,30);
  color:white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#card{height:300px;width:200px;border-radius:6px;overflow:hidden;transition:all 200ms ease;cursor:pointer}#card:hover{-webkit-transform:scale(1.08);transform:scale(1.08);text-decoration:underline}#card img{width:100%;height:80%;border-radius:6px}#card h4{margin:0}
#grid{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-around;padding:5px}#main{margin-top:100px}
nav{padding:10px}nav input{background-color:#06121e;border:1px solid #18406b;border-radius:4px;text-align:left;padding:10px;color:#fff;outline:none;font-family:"Zen Kaku Gothic Antique",sans-serif}nav input::-webkit-input-placeholder{color:#18406b}nav input:-ms-input-placeholder{color:#18406b}nav input::placeholder{color:#18406b}#navbar{z-index:9999999;display:flex;position:fixed;top:0;right:0;left:0;align-items:center;background-color:#0f2a46}#navbar img{margin:10px;width:70px;height:70px;border-radius:50px;cursor:pointer}
#images{overflow-y:auto}#images img{width:100%}
#poster{margin:10px;float:left;border-radius:6px;height:400px;width:300px}#summary{width:100%;float:left;height:300px;overflow-y:auto}#episode-div{float:left;width:100%;display:grid;align-items:center;justify-content:center;grid-template-columns:repeat(auto-fit, 120px);grid-gap:10px;gap:10px}#episode-div button{background-color:transparent;color:#fff;border:0;outline:none;font-weight:bolder;font-family:"Zen Kaku Gothic Antique",sans-serif;cursor:pointer}#episode-div button:hover{text-decoration:underline}#info-parent{margin-top:100px}
